@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&family=Roboto:wght@300&display=swap');
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    color: rgb(152, 166, 173);
    font-family: 'Baloo Bhai 2', cursive;
}

img {
    padding: 10px 5px;
    margin: 45px 34px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.info p {
    color: white;
    font-family: 'Baloo Bhai 2', cursive;
    font-size: 1.25rem;
}

.info li{
    color: white;
    font-family: 'Baloo Bhai 2', cursive;
    font-size: 1.25rem;
}

.addInfo {
    display: flex;
}

.education {
    margin: auto;
}

.education p{
    font-size: 1rem;
    margin: 5px 0px;
    color:rgb(152, 166, 173)
}

.education ul{
    list-style: none;
}

#Contact{
    margin-top: 15vh;
}

.icons{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.icons a{
    margin: 5px;
}

section#Contact h1{
    max-width: 91px;
    margin: auto;
    font-size: 2rem;
}

@media (min-width: 768px) {
    .container{
        flex-direction: row;
    }
    p{
        font-size: 1.5rem;
    }
}